import axios from 'axios';
require('dotenv').config();
const BASE_URL = 'https://admin_api.specta.app/v1/private/admin/leader/';

const state = {
  list_data: [],
  pending_list: [],
  current_page: 1,
  selected: {
    month: 1,
    year: 2024,
    state: 'all',
  },
  approve: false,
  state_settings: {
    error: false,
    success: false,
    error_message: '',
    success_message: '',
  },
  pending_claim: false,
  sum_data: 0,
  data_leader: { id_user: 0, state: 'pending' },
  options_data_leader: [
    { text: 'pending', value: 'pending' },
    { text: 'all', value: 'all' },
    { text: 'reject', value: 'reject' },
  ],
  reward_leader_list: [{ data: 'data' }],
  option: {
    months: [
      { text: 'januari', value: 1 },
      { text: 'febuari', value: 2 },
      { text: 'maret', value: 3 },
      { text: 'april', value: 4 },
      { text: 'mei', value: 5 },
      { text: 'juni', value: 6 },
      { text: 'juli', value: 7 },
      { text: 'agustus', value: 8 },
      { text: 'september', value: 9 },
      { text: 'oktober', value: 10 },
      { text: 'november', value: 11 },
      { text: 'december', value: 12 },
    ],
    years: [
      { value: 2024, text: 2024 },
      { value: 2025, text: 2025 },
      { value: 2026, text: 2026 },
      { value: 2027, text: 2027 },
      { value: 2028, text: 2028 },
      { value: 2029, text: 2029 },
    ],
    states: [
      { value: 'all', text: 'all' },
      { value: 'rejected', text: 'rejected' },
    ],
  },
};

const mutations = {
  SET_LIST(state, _source) {
    state.list_data = _source;
  },
  SETSUMDATA(state, _source) {
    state.sum_data = _source;
  },
  SETPENDING(state, _source) {
    state.pending_claim = _source;
  },
  SET_USER_ID(state, _source) {
    state.data_leader.id_user = _source;
  },
  SET_DETAILLIST(state, _source) {
    state.reward_leader_list = _source;
  },
  SET_APPROVE(state, _source) {
    state.approve = _source;
  },
  SET_PAGE(state, _source) {
    state.current_page = _source;
  },
  SET_PENDINGLIST(state, _source) {
    state.pending_list = _source;
  },
  SET_STATS(state, _source) {
    state.state_settings.error = _source.error;
    state.state_settings.success = _source.success;
    state.state_settings.error_message = _source.error_message;
    state.state_settings.success_message = _source.success_message;
  },
};

const actions = {
  async getList({ state, commit }) {
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`${BASE_URL}list_date?month=${state.selected.month}&year=${state.selected.year}&claim_state=${state.selected.state}`, { headers });
      console.log({ respone: response.data.data });
      commit('SET_LIST', response.data.data);
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  async getListPending({ commit }) {
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`${BASE_URL}pending_approval`, { headers });
      console.log({ respone: response.data.data });
      commit('SET_PENDINGLIST', response.data.data);
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  setUserId({ commit }, _data) {
    commit('SET_USER_ID', _data);
  },
  setPending({ commit }, _data) {
    commit('SETPENDING', false);
  },

  async getPendingData({ commit, state }) {
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`${BASE_URL}data_leader?id_user=${state.data_leader.id_user}&state=${state.data_leader.state}`, { headers });

      await commit('SET_DETAILLIST', response.data.data);
      if (response.data.data.length > 0) {
        const sumData = response.data.data.reduce((acc, item) => acc + parseFloat(item.amount), 0);
        await commit('SETSUMDATA', sumData);
        await commit('SETPENDING', true);
      }
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  async getAllData({ commit, state }) {
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`${BASE_URL}data_leader?id_user=${state.data_leader.id_user}&state=all`, { headers });
      console.log({ respone: response.data.data });
      await commit('SET_DETAILLIST', response.data.data);
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  async getRejectData({ commit, state }) {
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.get(`${BASE_URL}data_leader?id_user=${state.data_leader.id_user}&state=rejected`, { headers });
      console.log({ respone: response.data.data });
      await commit('SET_DETAILLIST', response.data.data);
    } catch (error) {
      console.log('Error:', error.response.data);
      return error.response.data;
    }
  },
  async approveReward({ commit, state }) {
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.post(
        `${BASE_URL}approve`,
        {
          id_user: state.data_leader.id_user,
          state: 'accept',
        },
        { headers }
      );
      const state_settings = {
        error: false,
        success: true,
        error_message: '',
        success_message: `approve transaction with id ${_data} has been accepted`,
      };
      await commit('SETPENDING', false);
      commit('SET_STATS', state_settings);
      commit('SET_APPROVE', false);
      console.log(response);
      return;
    } catch (error) {
      console.log({ error: error });
      const state_settings = {
        error: true,
        success: false,
        error_message: error.response.data.message,
      };
      commit('SET_STATS', state_settings);
      return error;
    }
  },
  async rejectReward({ commit, state }, _data) {
    try {
      const jwtToken = localStorage.getItem('jwtToken');
      if (!jwtToken) {
        console.error('Token JWT tidak ditemukan di localStorage.');
        return null;
      }
      const headers = {
        Authorization: `Bearer ${jwtToken}`,
      };
      const response = await axios.post(
        `${BASE_URL}approve`,
        {
          id_user: state.data_leader.id_user,
          state: 'rejected',
        },
        { headers }
      );
      const state_settings = {
        error: false,
        success: true,
        error_message: '',
        success_message: `reject transaction with id ${_data} has been accepted`,
      };
      await commit('SETPENDING', false);
      commit('SET_STATS', state_settings);
      commit('SET_APPROVE', false);
      return;
    } catch (error) {
      commit('SET_APPROVE', false);
      return;
      console.log('Error:', error.response);
      return error.response;
    }
  },
};
const getters = {
  listData: (state) => state.list_data,
  pendingData: (state) => state.pending_list,
  stateData: (state) => state.state_settings,
  pageData: (state) => state.current_page,
  rewardList: (state) => state.reward_leader_list,
  pendingCLaim: (state) => state.pending_claim,
  sumData: (state) => state.sum_data,
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
